                            :interval=1
                            @change="populateData('values')"
                          >
                            <!-- @change="clearErrorMsg" -->
                            <template v-slot:tooltip="{ value, focus }">
                              <div
                                :class="['custom-tooltip', { focus }]"
                              >{{ new Date(value *1000).toISOString().split('T')[0] }}</div>
                            </template>
                          </vue-slider>
                        </b-col>
                      </b-row>
                    </b-col>
                  </b-row>

                  <!-- table -->
                  <b-row class="no-scroll shadow m-1 bg-white rounded" v-if="maindata.length >0">
                    <b-col class="table-scroll" style="margin-top:10px;">
                      <table class="table">
                        <tr>
                          <th class="font-weight-bold" style="width: 100px;">Date</th>
                          <th v-for="(test, index) in maindata" :key="`,cost_Rebaritem-${index}`">
