                        </b-container>
                      </p>
                    
                    </div>
                  </div>
                  <div v-else>
                 
                     <!-- <p class="margin-div">
                       <p>yet to be displayed...</p>
                      </p> -->
                  </div>
                </div>
                  <div v-else-if="newsLoading" class="no-news-div">
                    <Spinner/>
                  </div>
                  <div v-else class="no-news-div">
                    <h4>No news to show...</h4>
                  </div>
                </b-col>
              </b-row>
            </b-col>
          </b-row>
        </b-col>
      </b-row>

    </b-container>
    <!--Modal-->
    <b-modal v-model="modalcommon" size="lg" title="Graphs of Zone" lazy>
      <b-card no-body>
        <b-tabs pills card>
          <b-tab title="Chart" active>
            <div class="myChartDiv">
              <!-- <chart :chart-data="datacollection" :width="250"></chart> -->
            </div>
          </b-tab>
          <b-tab title="Raw Data">
            <b-container>
              <b-row class="space-row">
                <b-col class="table-scroll">
                  <table class="table">
                    <thead style="position: fixed;">
                      <tr>
                        <th>Date</th>
                        <th>APAC</th>
                        <th>NAZ</th>
                        <th>South Africa</th>
                        <th>Europe</th>
                      </tr>
                    </thead>
                    <tr v-for="(test, index) in prices" :key="`,list-${index}`">
                      <td>{{test.Time}}</td>
                      <td>{{test.Price}}</td>
                      <td>{{test.Random}}</td>
                      <td>{{test.Random1}}</td>
                      <td>{{test.Random2}}</td>
                    </tr>
