</template>

<script>
// import JQuery from "jquery";
// let $ = JQuery;
import VueSlider from "vue-slider-component";
import "vue-slider-component/theme/default.css";
import Chart from "@/charts/chart.js";
import Restapi from "@/services/Restapi";
// import { RiseLoader } from "@saeris/vue-spinners";
import Spinner from "./Spinner";
import material_json from "@/modules/material";
// import ChatBox from './ChatBox'
import WebChat from "./web-chat/WebChat";

export default {
  name: "Home",
  components: {
    Chart,
    // RiseLoader,
    VueSlider,
    Spinner,
    // ChatBox,
    WebChat
  },
  data() {
    return {
      myChart: null,
      dateSlider: [],
      // initial values of slider dots - sets one day less than supplied date
      SliderValues: [
        new Date("" + new Date().getFullYear() - 1 + ".01.01").getTime() / 1000,
        new Date(
          "" +
            new Date().getFullYear() +
            "." +
            new Date().getMonth() +
            "." +
            new Date().getDate() +
            ""
        ).getTime() / 1000
      ],
      // minimum range of slider - sets one day less than supplied date
      sliderminRange:
        new Date("" + new Date().getFullYear() - 1 + ".01.01").getTime() / 1000,
      // maximum range of slider - - sets one day less than supplied date
      slidermaxRange:
        new Date(
          "" +
            new Date().getFullYear() +
            "." +
            new Date().getMonth() +
            "." +
            new Date().getDate() +
            ""
        ).getTime() / 1000,
      selected: "D", // ????
      modalcommon: false,
      datacollection: null, // collection of data to be displayed on chart
      dataSlider: null,
      news: [], // news gathered here for selected material
      hasNews: false, // tests whether news is available or not
      news1:true,//for news toggle
      newsLoading: true, // tests whether news is loading or not
      chartLoading:false, // tests whether a chart is being loaded
      maindata: [], // populated from populateData() - for prices data
      test2: [],
      prices: [], // price data gathered here for selected material
      materials_list: material_json.Materials, // initial list of materials / saved here
      showchart: false, // toggles chart visibility
      selected_material: "", // currently selected material
    
    };
  },

  mounted() {
    // initialize values on component mounting
    // this.getSliderValues().then(values => {
    //   this.SliderValues = values;
    //   console.log(this.SliderValues);
    // });
    this.$nextTick(() => {
      this.showchart = false;
      this.select_material(this.materials_list[0]);
      // this.populateData();
      // console.log(this.SliderValues);
    });
  },

  filters: {
    randomVal: function() {
      // not used - same as methods.randomVal()
      return Math.floor(Math.random(2) * (4500 - 3500) + 3500, 2);
    }
  },

  methods: {
    /* eslint-disable */

    // used to check whether a nav tab (material) is active - for bootstrap-vue
    checknavactive(material) {
      if (this.selected_material.api === material.api) {
        return true;
      } else {
        return false;
      }
    },

    getSliderValues() {
      let values = [];
      return Restapi.GetPrices(this.materials_list[0].param).then(prices => {
        this.SliderValues[0] = prices.data[0].Time;
        this.SliderValues[1] = prices.data[prices.data.length - 1].Time;

        values[0] = prices.data[0].Time;
        values[1] = prices.data[prices.data.length - 1].Time;
        return values;
        // this.SliderValues[0] = prices.data[0].Time;
        // this.SliderValues[1] = prices.data[prices.data.length - 1].Time;
      });
    },
  

    // used to select material from given list - calls getapidata()
    select_material(material) {
      this.selected_material = material;
      this.getapidata(material);
    },

    // fetches data from node server / uses material.list or material.param as query payload
    // then calls populateNewsData() & populatePriceData() to fill the data
    getapidata(material) {
      this.hasNews = false;
      this.newsLoading = this.chartLoading = true;
      Restapi.GetNews(material.list).then(news => {
        this.newsLoading = false;
        this.populateNewsData(news);
      });
      Restapi.GetPrices(material.param).then(prices => {
           console.log(prices);
        this.chartLoading = false;
        if (prices.data[0].Material === this.selected_material.param) {
          this.populatePriceData(prices);
          // this.SliderValues[0] = prices[0].Time;
          // this.SliderValues[0] = prices[prices.length - 1].Time;
        }
      });
    },

    // populates news data for news div of selected material
    populateNewsData(news) {
      this.news = [];
      this.hasNews = news.data.length > 0;
      if (this.hasNews) {
        news.data.forEach(item => {
          if (item.Score && item.Headline != "") {
            this.news.push({
              Headline: item.Headline,
              score: item.Score,
              link: item.URL,
              date: item.Date,
              status: item.Status
            });
          }
        });
      } else {
        this.hasNews = false;
      }
    },

    // populates prices of selected material - calls populateData() to fill maindata[]
    populatePriceData(prices) {
      this.prices = [];
      prices.data.forEach(element => {
        this.prices.push({
          Time: element.Time,
          Price: parseInt(element.Price),
          naz_price: this.randomVal(),
          south_africa_price: this.randomVal(),
          europe_price: this.randomVal() + 400,
          Cost: element.Cost,
          Profit: parseInt(element.Price) - element.Cost
        });
      });
      this.chartLoading = false;
      this.populateData(this.prices);
    },

    // generates random values for other regions b/w 3500 to 5000
    randomVal: function(val) {
      return Math.floor(Math.random(2) * (5000 - 1500) + 1500, 2);
    },

    // populates maindata[] based upon raw-data value (first -> weekly / second -> monthly / _ -> all data) // draws the new chart
    // calls makeChartData() to fill chart data
    populateData(raw_data) {
      let last_month = new Date().getMonth() + 1 + "";
      let avg = 0;
      let arr = this.prices;
      if (raw_data == "first") {
        // weekly filter
        arr = [];
        for (let index = 0; index < this.prices.length; index++) {
          const element = this.prices[index];
          let day = new Date(element.Time).getDay();
          if (day == 1 || index == 0) {
            avg = element.Price;
          } else {
            avg = (avg + element.Price) / 2;
          }
          if (day == 0) {
            arr.push({
              Price: avg,
              Time: new Date(element.Time).toDateString().slice(3, 15),
              naz_price: this.randomVal(),
              south_africa_price: this.randomVal(),
              europe_price: this.randomVal(),
              Cost: element.Cost,
              Profit: element.Profit
            });
            avg = 0;
          }
        }
      } else if (raw_data == "second") {
        // monthly filter
        arr = [];
        for (let index = 0; index < this.prices.length; index++) {
          const element = this.prices[index];
          let date = parseInt(element.Time.slice(9, 10));
          let month = parseInt(element.Time.slice(5, 7));
          let year = parseInt(element.Time.slice(0, 4));
          if (month == last_month) {
            if (date == 1) {
              avg = element.Price;
            } else {
              avg = (avg + element.Price) / 2;
            }
          } else {
            arr.push({
              Price: avg,
              Time: new Date(element.Time).toDateString().slice(3, 15),
              naz_price: this.randomVal(),
              south_africa_price: this.randomVal(),
              europe_price: this.randomVal(),
              Cost: element.Cost,
              Profit: element.Profit
            });
            last_month = month;
            avg = 0;
            index--;
          }
        }
      } else if (raw_data == "values") {
        arr = [];
        this.prices.forEach((element, index) => {
          if (
            element.Time >
              new Date(this.SliderValues[0] * 1000)
                .toISOString()
                .split("T")[0] &&
            element.Time <
              new Date(this.SliderValues[1] * 1000).toISOString().split("T")[0]
          ) {
            // console.log(
            //   new Date(this.SliderValues[1] * 1000).toISOString().split("T")[0]
            // );
            arr.push({
              Time: new Date(element.Time).toDateString().slice(3, 15),
              Price: element.Price,
              naz_price: this.randomVal(),
              south_africa_price: this.randomVal(),
              europe_price: this.randomVal(),
              Cost: element.Cost,
              Profit: element.Profit
            });
          }
        });
      } else {
        // all data filter
        arr = [];
        for (let index = 0; index < this.prices.length; index++) {
          const element = this.prices[index];
          arr.push({
            Time: new Date(element.Time).toDateString().slice(3, 15),
            Price: element.Price,
            naz_price: this.randomVal(),
            south_africa_price: this.randomVal(),
            europe_price: this.randomVal(),
            Cost: element.Cost,
            Profit: element.Profit
          });
        }
      }
      // console.log(this.maindata);
      this.maindata = arr;
      this.makeChartData();
    },

    // provides data to chart
    makeChartData() {
      let time_labels = [];
      let APAC_price = [];
      let South_Africa_price = [];
      let NAZ_price = [];
      let Europe_price = [];
      this.maindata.forEach((element, i) => {
        {
          time_labels.push(element.Time);
          APAC_price.push(element.Price);
          NAZ_price.push(element.naz_price);
          South_Africa_price.push(element.south_africa_price);
          Europe_price.push(element.europe_price);
        }
      });
      let pointRadius = 2;
      let borderWidth = 1;
      this.datacollection = {
        labels: time_labels,
        responsive: true,
        maintainAspectRatio: false,
        datasets: [
          {
            label: "APAC",
            // backgroundColor: "#f87979",
            backgroundColor: "rgba(248, 79, 79, 1)",
            data: APAC_price,
            fill: false,
            borderColor: "rgba(248, 79, 79, 1)",
            borderWidth: borderWidth,
            pointRadius: pointRadius
          },
          {
            label: "NAZ",
            backgroundColor: "#e9d743",
            data: NAZ_price,
            borderColor: "#e9d743",
            fill: false,
            borderWidth: borderWidth,
            pointRadius: pointRadius
          },
          {
            label: "South-Africa",
            backgroundColor: "#8eddf0",
            data: South_Africa_price,
            fill: false,
            borderColor: "#8eddf0",
            borderWidth: borderWidth,
            pointRadius: pointRadius
          },
          {
            label: "Europe",
            backgroundColor: "#9Abb92",
            data: Europe_price,
            fill: false,
            borderColor: "#9Abb92",
            tooltips: true,
            borderWidth: borderWidth,
            pointRadius: pointRadius
          }
        ]
      };
      this.showchart = true;
    },

    onChartReady() {
      this.showChart = false;
    },

    toggleNews:function(){
       this.news1=!this.news1;
    },

    // sorts news based on score
    sort: function(val) {
      if (val === "asce") {
        this.news.sort((a, b) => {
          var num1 = a.score.slice(0, a.score.length - 1);
          var num2 = b.score.slice(0, b.score.length - 1);
          if (parseInt(num1) > parseInt(num2)) {
            return 1;
          } else {
            return -1;
          }
        });
      } else if (val === "desc") {
        this.news.sort((a, b) => {
          var num1 = a.score.slice(0, a.score.length - 1);
          var num2 = b.score.slice(0, b.score.length - 1);
          if (parseInt(num1) < parseInt(num2)) {
            return 1;
          } else {
            return -1;
          }
        });
      }
    },

    // sorts news based on date
    sortDate: function() {
      var sortScore = this.news;
      this.news.sort((a, b) => {
        if (new Date(a.date).getTime() > new Date(b.date).getTime()) return 1;
        else return -1;
      });
    },

    sortdateDesc: function() {
      var sortScore = this.news;
      this.news.sort((a, b) => {
        if (new Date(b.date).getTime() > new Date(a.date).getTime()) return 1;
        else return -1;
      });
    },

    // get height of chart to render based on screen height - used to make page dynamic
    getChartHeight() {
      console.log($(window).height());
      let height = $(window).height();
      if (height > 900) return 540;
      else if (height > 800) return 420;
      else if (height > 700) return 320;
      else return 260;
    },

    formatDate(date) {
      date = date.trim();
      let dates = date.split(" ");
      let month = dates[0].substring(0, 3);
      let day = dates[1].includes(",")
        ? dates[1]
        : dates[1].substring(0, 2) + ",";
      let year = dates[2].substring(2);
      let formattedDate = month + " " + day + " " + year;
      return formattedDate;
    }
  }
};
</script>

