import Vue from "vue";

export default {
  GetNews(payload) {
      return new Promise((resolve, reject) => {
      Vue.axios({
        method: "POST",
        // url: "http://localhost:3030/news",
         url: "https://eymarketintelapi.eu-gb.mybluemix.net/news",
        // test
        headers: {
          "Content-Type": "application/json"
        },
        data: {
          query: payload
        }
      })
        .then(function(response) {
          resolve(response);
        })
        .catch(error => {
          reject(error);
        });
    });
  },
  GetPrices(payload) {
    return new Promise((resolve, reject) => {
      Vue.axios({
        method: "POST",
        url: "http://137.116.113.220:3030/prices",
        // url: "https://eymarketintelapi.eu-gb.mybluemix.net/pricesbymaterial",
        headers: {
          "Content-Type": "application/json"
        },
        data: {
          query: payload
        }
      })
        .then(function(response) {
          resolve(response);
        })
        .catch(error => {
          reject(error);
        });
    });
  }
};
