import { Line, mixins } from "vue-chartjs";
import "chartjs-plugin-zoom";
const { reactiveProp } = mixins;

export default {
  extends: Line,
  mixins: [reactiveProp],
  props: ["chartData", "height", "width", "minPanDate", "maxPanDate", "zoomMonths"],
  data() {
    return {
      options: {
        height: this.height,
        width: this.width,
        scales: {
          yAxes: [
            {
              ticks: {
                // beginAtZero: true,
              },
              gridLines: {
                display: false
              }
            }
          ],
          xAxes: [
            {
              gridLines: {
                drawOnChartArea: true
              },
              type: "time",
              time: {
                min: '2019-03-01',
                unit: "month",
                unitStepSize: 1,
                displayFormats: {
                  day: "MMM DD"
                }
              }
            }
          ]
        },
        legend: {
          display: true
        },
        responsive: true,
        maintainAspectRatio: false,
        /* hover: {
          onHover: function(event, elements) {
            if (elements.length > 0)
              console.log(elements);
          }
        }, */
        plugins: {
          zoom: {
            pan: {
              enabled: true,
              mode: "x",
              rangeMin: {
                x: new Date('2017-12-31') // chart.scales["x-axis-0"].min.valueOf(),
                // x: new Date(this.minPanDate), // use this when actual scale values (data) are available
              },
              rangeMax: {
                x: new Date('2019-05-05') // chart.scales["x-axis-0"].max.valueOf(),
                // x: new Date(this.maxPanDate), // use this when actual scale values (data) are available
              },
            },
            zoom: {
              enabled: true,
              drag: false,
              mode: "x",
              speed: 0.3,
              rangeMin: {
                x: new Date('2017-12-31') // chart.scales["x-axis-0"].min.valueOf(),
              },
              rangeMax: {
                x: new Date('2019-05-05') // chart.scales["x-axis-0"].max.valueOf(),
              },
              // },
              // onZoom: function({ chart }) {
              //   console.log(chart);
              //   console.log(`I was zoomed!!!`);
              // }
            }
          }
        }
      }
    };
  },
  mounted() {
    this.renderChart(this.chartData, this.options);
    },
};
