import Vue from 'vue';
import App from './App.vue';
import router from './router';
import BootstrapVue from "bootstrap-vue";
import axios from "axios";
import VueAxios from "vue-axios";
import Jquery from "jquery";
import VueSlider from "vue-slider-component";
import "vue-slider-component/theme/default.css";
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";
import "font-awesome/css/font-awesome.min.css";
import "./assets/styles/App.css";
import { library } from '@fortawesome/fontawesome-svg-core'
import { faUserSecret, faPaperPlane, faComment, faTimes, faCommentAlt, faWindowMinimize, faExchangeAlt } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

global.Jquery = Jquery;
global.$ = Jquery;

Vue.config.productionTip = false;

Vue.use(BootstrapVue);
Vue.use(VueAxios, axios);

Vue.component("VueSlider", VueSlider);
Vue.component('font-awesome-icon', FontAwesomeIcon);
library.add(faUserSecret, faPaperPlane, faComment, faTimes, faCommentAlt, faWindowMinimize, faExchangeAlt);


new Vue({
  router,
  render: h => h(App)
}).$mount('#app');
