<template>

  <div id="container-div">

    <!-- top action bar -->
    <div id="top-action-bar" v-show="open" >
      <font-awesome-icon icon="exchange-alt" :style="{ color: 'white', marginRight: '20px', }" @click="toggleSide" />
      <font-awesome-icon icon="window-minimize" :style="{ color: 'white', marginBottom: '8px' }" @click="toggleChatbox" />
    </div>

    <!-- chat box -->
    <div id="botframework-chat" v-show="open" />

    <!-- toggler -->
    <div v-if="!open" class="toggle-btn">
      <b-button 
        pill
        @click="toggleChatbox"
        style="width: 50px; height: 50px; box-shadow: 3px 3px 5px #888; background-color: #39C; border: 0; padding: 15px;"
      >
        <font-awesome-icon icon="comment-alt" size="lg" />
      </b-button>
    </div>

  </div>

</template>

<script>
import {
  renderWebChat,
  createDirectLine,
  createStyleSet
} from "botframework-webchat";
import Vue from "vue";
import * as $ from "jquery";

export default {
  name: "web-chat",
  components: {},
  props: [],
  data() {
    return {
      token: null,
      open: false,
      styleSet: createStyleSet({
        // styles for webchat component
        backgroundColor: "white",
        border: "5px"
        // bubbleBackground: 'rgba(200, 200, 200, .2)', // styles for changing the card background
        // bubbleFromUserBackground: 'rgba(200, 200, 200, .2)',
      })
    };
  },

  computed: {},

  mounted() {
    /*
    // use only for testing when no token or secret is available
    this.FetchToken().then(token => {
      this.token = token.data.token;
      // change font of messages form user and bot
      this.styleSet.textContent = {
        ...this.styleSet.textContent,
        fontFamily: "'Comic Sans MS', 'Arial', sans-serif",
        fontWeight: 'bold'
      };
      renderWebChat(
        {
          directLine: createDirectLine({
            token: this.token 
          }),
          styleSet: this.styleSet,
          // activityMiddleware: this.activityMiddleware
        },
        document.getElementById("botframework-chat")
      );
    })
    */

    // use for prod - when secret / token is available
    renderWebChat(
      {
        directLine: createDirectLine({
          token: "sQIlsKbmC6s.-ZQp0APfKVVOmdQfKjwzvMH6pScLLhuBEWj2Y37K0hA"
        }),
        styleOptions: {
          hideUploadButton: true,
          backgroundColor: '#FFF',
        },
        // styleSet: this.styleSet,
      },
      document.getElementById("botframework-chat")
    );
  },

  methods: {
    // method to fetch mock token to test botframework
    // DO NOT USE this mthod in prod - provide your directLine secret or token in renderWebChat method istead
    FetchToken() {
      return new Promise((resolve, reject) => {
        Vue.axios({
          method: "POST",
          url: "https://webchat-mockbot.azurewebsites.net/directline/token",
          headers: {
            "Content-Type": "application/json"
          }
        })
          .then(function(response) {
            resolve(response);
          })
          .catch(error => {
            reject(error);
          });
      });
    },

    // open / close the chat box & toggle btn
    toggleChatbox() {
      if ($("#botframework-chat").css("display") === "flex") {
        $("#botframework-chat").css("display", "none");
        $("#container-div").css("left", "");
        $("#container-div").css("right", "30px");
      } else {
        $("#botframework-chat").css("display", "flex");
      }
      this.open = !this.open;
    },

    // change the side of chat box - left or right 30 px
    toggleSide() {
      if ($("#container-div").css("right") === "30px") {
        $("#container-div").css("right", "0");
        $("#container-div").css("left", "30px");
      } else {
        $("#container-div").css("left", "");
        $("#container-div").css("right", "30px");
      }
    },

    // to intercept the incoming card data from botframework server and render custom component
    // DOES NOT WORK - as it requires to return React.Element which in turn requires the react & reactDOM packages installed
    activityMiddleware: () => next => card => {
      return children => {
        // console.log("middleware invoked");
        if (card.activity.from.role === "user") {
          let div = document.createElement("DIV");
          div.style.cssText =
            "border-right-color:red;border-right-style:solid;border-right-width:5;margin-right:8;";
          div.innerHTML = next(card)(children);
          // console.log(div);
          return div; // return React.Element instead
        }
        let div = document.createElement("DIV");
        div.style.cssText =
          "border-left-color:red;border-left-style:solid;border-left-width:5;margin-left:8;";
        div.innerHTML = next(card)(children);
        // console.log("div", div);
        return div; // return React.Element instead
      };
    }
  }
};
</script>

